import { Inject, Injectable, InjectionToken } from '@angular/core';
import { FeatureFlags } from '../types/feature-flags';

export const FEATURE_FLAGS = new InjectionToken<FeatureFlags>('Feature Flags');

@Injectable({
  providedIn: 'root'
})
export class FeatureFlagsService {
  constructor(
    @Inject(FEATURE_FLAGS)
    private readonly _featureFlags: FeatureFlags
  ) {}

  public isEnabled(feature: string): boolean {
    return Boolean(this._featureFlags[feature]);
  }
}
